<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <schema-form
      :schema="schema"
      v-model="variables"
      @submit="handleSubmit()"
    />
  </div>
</template>

<script>
/* eslint-disable no-debugger */
/* eslint-disable no-unused-vars */

// import { CoreRestClient } from "azure-devops-extension-api/Core/CoreClient";

import * as SDK from "azure-devops-extension-sdk";
// import { GitServiceIds } from "azure-devops-extension-api/Git/GitServices";
import { getClient } from "azure-devops-extension-api";
// import { CoreRestClient } from "azure-devops-extension-api/Core";
import { GitRestClient } from "azure-devops-extension-api/Git/GitClient";

var gitzip = require("../gitzip");



export default {
  name: "HelloWorld",

  props: {
    msg: String,
  },
  computed: {},
  data() {
    return {
      scaffoldings: [
        {
          account: "scaffolding-repos",
          repo: "simple-nodejs",
          branch: "main",
        },
      ],
      selectedIdx: 0,
      variables: {},
      schema: {
        $id: "https://example.com/person.schema.json",
        $schema: "http://json-schema.org/draft-07/schema#",
        title: "Person",
        type: "object",
        properties: {
          firstName: {
            type: "string",
            title: "First Name",
            description: "The person's first name.",
            example: "John"
          },
          lastName: {
            type: "string",
            title: "Last Name",
            description: "The person's last name.",
            example: "Doe"
          },
          nickNames: {
            type: "array",
            title: "Nicknames",
            items: {
              type: "string",
              example: "j-dough"
            }
          },
          age: {
            title: "Age",
            description:
              "Age in years which must be equal to or greater than zero.",
            type: "integer",
            minimum: 0
          }
        },
        required: ["firstName"]
      },
    };
  },
  methods: {
    async loadScafdoling() {},
    async createRepo(repoName, project) {
      const client = getClient(GitRestClient);
      const repository = await client.createRepository(
        { name: repoName },
        project
      );
      await client.createPush(
        {
          refUpdates: [
            {
              name: "refs/heads/main",
              oldObjectId: "c913f4b6f62dd4ea7c5260a5385173c7a95f5110",
            },
          ],
          commits: [
            {
              comment: "Initial commit.",
              changes: [
                {
                  changeType: "add",
                  item: {
                    path: "/readme3.md",
                  },
                  newContent: {
                    content: "My third file!",
                    contentType: "rawtext",
                  },
                },
              ],
            },
          ],
        },
        repository.id,
        project
      );
    },
  },
  mounted() {
    // var octo = new Octokat();

    // debugger;
    // fetch(
    //   "https://github.com/scaffolding-repos/simple-nodejs/archive/refs/heads/main.zip",
    //   { mode: "no-cors", redirect: "follow" }
    // )
    //   // Handle success
    //   .then((response) => {
    //     if (response.redirected) {
    //       console.log(response.url)
    //     }
    //   }) // convert to json
    //   .then((response) => response.arrayBuffer()) // convert to json
    //   .then((json) => console.log(json)) //print data to console
    //   .catch((err) => console.log("Request Failed", err)); // Catch errors

    (async () => {
      try{
        await SDK.init();
      } catch(e) {
        console.log(e);
      }
      const scaffolding = this.scaffoldings[this.selectedIdx];
      const contents = await gitzip(
        scaffolding.account,
        scaffolding.repo,
        scaffolding.branch
      );
      const scaffoldingJSON = contents["scaffolding.json"];
      const scaffoldingSettings = JSON.parse(scaffoldingJSON);
      this.schema = {
        type: "object",
        properties: scaffoldingSettings.variables,
      };
    })();
  },
};
</script>

